.home-form{
    background-color: #666;
    padding: 20px  35px 40px;
    width: fit-content;
    display: flex;
    flex-flow: column;
    gap: 20px;
    color: #fff;
    font-family: sans-serif;
}
.home-form p {
    font-size: 20px;
}
.home-form input{
    padding: 15px;
    border-radius: 10px;
    background-color: #ccc;
    border: none;
    height: 55px;
    box-sizing: border-box;
    outline: 0;
}
.home-form button{
    padding: 15px 25px;
    width: 100px;
    margin: 0  auto;
    border-radius: 9px;
    border: none;
    font-weight: bold;
    background-color: #222;
    color: #fff;
}