/* slider ============================================================================================= */

  .embla {
    --slide-spacing: 1rem;
    --slide-size: 100%;
    --slide-height: 19rem;
    padding: 1.6rem;
  }
  .embla__viewport {
    overflow: hidden;
    overflow: hidden;

  }
  .embla__container {
    backface-visibility: hidden;
    display: flex;
    touch-action: pan-y;
    margin-left: calc(var(--slide-spacing) * -1);
    width: 80vw;
  }
  .embla__slide {
    flex: 0 0 var(--slide-size);
    min-width: 80vw;
    padding-left: var(--slide-spacing);
    position: relative;
  }
  .embla__slide__img {
    display: block;
    height: var(--slide-height);
    width: 100%;
    object-fit: contain;
  }
  .embla__slide__number {
    width: 4.6rem;
    height: 4.6rem;
    z-index: 1;
    position: absolute;
    top: 0.6rem;
    right: 0.6rem;
    border-radius: 50%;
    background-color: rgba(var(--background-site-rgb-value), 0.85);
    line-height: 4.6rem;
    font-weight: 900;
    text-align: center;
    pointer-events: none;
  }
  .embla__slide__number > span {
    color: var(--brand-primary);
    background-image: linear-gradient(
      45deg,
      var(--brand-primary),
      var(--brand-secondary)
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1.6rem;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .embla__slide:nth-child(1) {flex: 0 0 60%;}
  .embla__slide:nth-child(2) {flex: 0 0 40%;}
  .embla__slide:nth-child(3) {flex: 0 0 30%;}
  .embla__slide:nth-child(4) {flex: 0 0 90%;}
  .embla__slide:nth-child(5) {flex: 0 0 35%;}
  .embla__slide:nth-child(6) {flex: 0 0 55%;}
  .embla__slide:nth-child(7) {flex: 0 0 85%;}
  .embla__slide:nth-child(8) {flex: 0 0 46%;}
  .embla__slide:nth-child(9) {flex: 0 0 30%;}
  
  .slider-grid{
    display: grid;
    grid-template-columns: 10vw 80vw 10vw;
    /* grid-template-rows: ; */
    place-items: center;
  }
  .slider-prev,.slider-next{
    z-index: 10;
    height: fit-content;
    border: none;
    background-color: transparent;
    backdrop-filter: blur(19px);
    font-size: 20px;
    font-weight: 100;
    text-align: center;
    padding: 17px;
    border-radius: 50%;
}

.slider-next{
    /* height: fit-content;
    z-index: 10; */
    position: absolute;
    right: 0;
  }
